"use client";

import React, { useState } from "react";
import { Calendar as CalendarComponent } from "@/components/ui/calendar";
import { format } from "date-fns";

const Dashboard = ({ appointments, userType }) => {
  const [selectedDate, setSelectedDate] = useState(new Date());

  // Filter appointments for the selected date (ignoring time)
  const appointmentsForDate = appointments.filter((app) => {
    return format(new Date(app.date), "yyyy-MM-dd") === format(selectedDate, "yyyy-MM-dd");
  });

  // Total appointments and unique doctor names
  const totalAppointments = appointments.length;
  const doctorNames = [...new Set(appointments.map((app) => app.doctorName))];

  return (
    <div className="p-8">
      <h1 className="text-3xl font-bold mb-8">Dashboard</h1>

      {/* Info Boxes */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <div className="bg-white p-4 rounded shadow">
          <h2 className="text-xl font-semibold">Total Appointments</h2>
          <p className="text-2xl">{totalAppointments}</p>
        </div>
        <div className="bg-white p-4 rounded shadow">
          <h2 className="text-xl font-semibold">Doctors</h2>
          <ul>
            {doctorNames.map((name, idx) => (
              <li key={idx} className="text-lg">{name}</li>
            ))}
          </ul>
        </div>
      </div>

      {/* Calendar Grid & Daily Appointment Details */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        {/* Calendar occupies 80% (3/4 columns) */}
        <div className="md:col-span-3">
          <CalendarComponent
            mode="single"
            selected={selectedDate}
            onSelect={setSelectedDate}
          />
        </div>
        {/* Sidebar showing appointments on the selected date */}
        <div className="md:col-span-1 bg-white p-4 rounded shadow">
          <h3 className="text-xl font-semibold mb-4">
            Appointments on {format(selectedDate, "PPP")}
          </h3>
          {appointmentsForDate.length > 0 ? (
            <ul>
              {appointmentsForDate.map((app) => (
                <li key={app.id} className="mb-2 border-b pb-1">
                  <p className="font-semibold">{app.title}</p>
                  <p className="text-sm text-gray-500">{app.time}</p>
                </li>
              ))}
            </ul>
          ) : (
            <p>No appointments on this day.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
